.question {
  width: 100%;
  margin: 125px 0 170px;
}

.question__wrapper {
  display: flex;
  justify-content: flex-end;
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 25px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    background: url("../img/form-img.png") no-repeat;
    background-color: #febca1;
    background-size: cover;
    width: 844px;
    height: 455px;
    left: 25px;
    top: 0;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/form-img2x.png");
      background-size: 844px 455px;
    }
  }
}

.question-form {
  display: flex;
  flex-direction: column;
  justify-content: right;
  align-items: center;
  background-color: $white;
  border-radius: 5px;
  box-shadow: 0 4px 30px rgba(41, 54, 66, 0.1);
  width: 656px;
  padding: 50px 30px;
  z-index: 10;
  margin: 70px 0;

  h3 {
    font-size: 25px;
    font-weight: 600;
    line-height: 32px;
    letter-spacing: 0;
    padding-bottom: 13px;
    color: $dark;
  }

  p {
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: 0;
    padding-bottom: 25px;
    color: $dark;
  }
}

.question__wrapper form {
  width: 558px;
}

.form-input {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;

  input {
    width: 269px;
    height: 50px;
    border: 1px solid $grey;
    padding: 16px 15px;
    position: relative;

    &::placeholder {
      color: $dark;
      font-size: 14px;
      font-weight: 400;
    }
  }
}

.form-input-btn {
  display: flex;
  justify-content: center;
}

.form-btn {
  width: 176px;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 2px;
  padding: 18px 0;
  text-transform: uppercase;
  text-align: center;
  background-color: $accentMain;
  color: $white;
  cursor: pointer;

  &:hover,
  &focus {
    background-color: $accentHover;
  }

  &:active {
    opacity: 0.8;
  }
}

@media screen and (max-width: 1023px) {
  .question {
    width: 100%;
    margin: 0 0 80px;
  }

  .question__wrapper {
    justify-content: center;
    padding: 0 25px;
    position: relative;

    &::before {
      width: 100%;
      right: 0;
      left: 0;
      background-position: center;
      background-size: cover;
    }
  }

  .form-btn {
    width: 250px;

    &:hover,
    &focus {
      background-color: $accentMain;
    }

    &:active {
      background-color: $accentHover;
      opacity: 1;
    }
  }
}

@media screen and (max-width: 767px) {
  .question {
    margin: 50px 0 70px;
  }

  .question__wrapper {
    width: 100%;
    padding: 50px 16px;
    justify-content: center;
    background-color: $light;

    &::before {
      display: none;
    }

    form {
      width: 100%;
    }
  }

  .question-form {
    width: 100%;
    text-align: center;
    background-color: transparent;
    box-shadow: none;
    margin: 0;
    padding: 0;

    h3 {
      padding-bottom: 20px;
    }
  }

  .form-input {
    flex-direction: column;
    align-items: center;
    margin-bottom: 0;

    input {
      width: 100%;
      margin-bottom: 20px;
    }
  }

  .form-btn {
    width: 288px;
  }
}
